<template>
  <div class="middle-block">
    <div class="info-block">
      <Info :message="foundMoviesMessage" />
    </div>
    <div class="buttons-block">
      <FilterButtons
        message="SORT BY"
        firstButtonTitle="RELEASE DATE"
        secondButtonTitle="RATING"
        firstOption="releaseDate"
        secondOption="rating"
        options="sort-option"
      />
    </div>
  </div>
</template>

<script>
import Info from "@/components/Info.vue";
import FilterButtons from "@/components/FilterButtons.vue";
import { mapGetters } from "vuex";

export default {
  components: {
    Info,
    FilterButtons
  },
  computed: {
    ...mapGetters(["moviesCount"]),
    foundMoviesMessage() {
      if (!this.moviesCount) {
        return "No movies found";
      } else if (this.moviesCount === 1) {
        return this.moviesCount + " movie found";
      }
      return this.moviesCount + " movies found";
    }
  }
};
</script>

<style scoped>
.middle-block {
  background-color: #555555;
  width: 100%;
  display: flex;
  padding: 10px 5px;
  margin: 0 auto;
}

.info-block {
  padding-left: 40px;
  margin-bottom: auto;
  margin-top: auto;
}

.buttons-block {
  margin-bottom: auto;
  margin-top: auto;
  margin-left: auto;
  padding-right: 40px;
  order: 2;
}
</style>
